.track-part {

  border-bottom: 1px solid #e6e6e6;
  border-right: 1px solid #e6e6e6;

  padding: 8px;
}

.track-header, .track-display-wrapper {
  height: 120px;
}

.track-header {
  background: #f2f2f2;
  width: 150px;
}

.track-display-wrapper {
  min-width: fit-content;
}

.track-display {
  background: white;
  height: 100%;
}

.track-canvas-wrapper {
  background: #80dbff;
  border-radius: 8px;
  height: 100%;
  /* opacity: 0.4; */
}

.track-canvas {
  /* background: #80dbff; */
  /* border-radius: 8px; */
  height: 100%;
  opacity: 0.4;
  width: 100%;
}

.delete-track {
  background: red;
  color: white;
  font-size: 2em;
  padding: 8px;
}
